import {useState} from 'react';

const useFade = () => {

    const [fade, seFade] = useState(true);
    const [change, seChange] = useState(true);

    function fadeInOut(action) {
        if (fade === true) {
            action()
            seFade(false)
            setTimeout(() => {
            seChange(!change)
            seFade(true)
            }, 650);
        }
    }

    return [fade,change,fadeInOut]
}

export default useFade